@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
:root{--primary: #10CD45;--grey: #9DADB9;--line-table: #EEF2F5;--text-red: #E44067;--text-table: #414141;--text-header-table: #878787;--text-black: #000000;--default: #FFFFFF}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font-family:'Lato', sans-serif;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.container-router{display:flex}.container-router .router-pages{width:100%;padding:55px 62px 0 62px}

.nav-container .nav-menu-items .navbar-toggle>.action p,.nav-container .btn-logout p{font-weight:bold;font-size:16px;line-height:19px;color:#FFFFFF;margin-left:20px}.nav-container{height:100vh;width:300px;background:linear-gradient(175.93deg, #5F00D8 -9.98%, #B200F1 117.29%);display:flex;flex-direction:column;justify-content:space-between;padding-left:33px}.nav-container .img-container{margin-top:40px}.nav-container .img-container .img-logo{width:40px;height:40px}.nav-container .img-container .line{width:80%;opacity:0.2;margin:35px 0;border:1px solid #FFFFFF}.nav-container .nav-menu-items{flex-grow:2}.nav-container .nav-menu-items .navbar-toggle>.action{text-decoration:none;display:flex;margin:20px 0}.nav-container .nav-menu-items .navbar-toggle>.action:hover p{opacity:0.8}.nav-container .btn-logout{flex-grow:1;display:flex;align-items:center;border:none;background-color:transparent;cursor:pointer}.nav-container .btn-logout:focus{outline:none}@media (max-width: 768px){.nav-container{width:50px;align-items:center;padding-left:0}.nav-container .img-container{margin-top:30px}.nav-container .img-container .img-logo{width:25px;height:25px}.nav-container .navbar-toggle>.action p{display:none}.nav-container .btn-logout>p{display:none}}

.actions .center p,.actions .center span{font-weight:500;font-size:14px;line-height:17px;color:var(--text-table)}.actions{display:flex;justify-content:center;margin-top:20px}.actions .btn{background-color:transparent;cursor:pointer;border:none}.actions .btn:focus{outline:none}.actions .center{display:flex;align-items:center;margin:0 10px}.actions .center p{width:35px;height:35px;background:var(--line-table);border-radius:5px;display:flex;align-items:center;justify-content:center}.actions .center span{margin:0 10px}

.tbl-body .data{min-width:180px;padding:15px 0}.tbl-body .profits{color:var(--primary)}.tbl-body .losses{color:var(--text-red)}.tbl-body:nth-child(even){background-color:var(--line-table)}.tbl-body:hover{cursor:pointer;background-color:var(--line-table)}

.use-image{display:flex;align-items:center;position:relative}.use-image img{position:absolute;width:30px;height:30px;margin-left:10px;border-radius:30px}.use-image span{margin-left:50px}

.tbl-header>.line-header>.title{text-align:start;font-weight:500;font-size:16px;line-height:19px;color:var(--text-header-table);padding:14px 14px 14px 0;z-index:1}

.container::-webkit-scrollbar{width:8px}.container::-webkit-scrollbar-thumb{background-color:var(--text-header-table);border-radius:20px}.container{width:100%;height:75vh;max-height:75vh;overflow-y:scroll}.container .customers{width:100%;border-spacing:8px 2px}.container .customers thead th{position:-webkit-sticky;position:sticky;top:0;background-color:var(--default)}

.header-container{display:flex;justify-content:space-between}.header-container p{font-size:39px;line-height:47px;color:var(--text-black)}.header-container .action-header{display:flex}.header-container .action-header .btn{width:67.45px;height:35.5px;border-radius:5px;border:none;background-color:transparent;cursor:pointer;font-weight:bold;font-size:14px;line-height:17px;text-align:center}.header-container .action-header .btn:focus{outline:none}.header-container .action-header .btn:hover{opacity:0.7}.header-container .action-header .btn-option{display:flex;justify-content:center;align-items:center;background-color:var(--line-table)}.header-container .action-header .btn-add{margin-left:15px;background-color:var(--primary);color:var(--default)}

.form-group{width:100%;display:flex;flex-direction:column;margin:0 20px 20px 0}.form-group label{font-weight:bold;font-size:14px;line-height:17px;color:var(--text-table)}.form-group input{border:none;border-bottom:1px solid var(--grey);border-radius:2px;color:var(--text-table);padding:6px;font-size:12px}.form-group input:focus{outline:none}.form-group input::-webkit-input-placeholder{color:#ccc}.form-group input:-ms-input-placeholder{color:#ccc}.form-group input::-ms-input-placeholder{color:#ccc}.form-group input::placeholder{color:#ccc}

.modal-container .create-user .header-form .btn-close,.modal-container .create-user .btn-add{border:none;background-color:transparent;cursor:pointer}.modal-container .create-user .header-form .btn-close:focus,.modal-container .create-user .btn-add:focus{outline:none}.modal-container .create-user .header-form .btn-close:hover,.modal-container .create-user .btn-add:hover{opacity:0.7}.modal-container{position:absolute;top:0;left:0;right:0;background-color:rgba(0,0,0,0.4);width:100%;height:100%;display:flex;justify-content:center;align-items:center;z-index:1}.modal-container .create-user{width:30%;padding:20px;background-color:var(--default);border-radius:5px;border:1px solid var(--line-table);display:flex;flex-direction:column;align-items:center}.modal-container .create-user .header-form{width:100%;display:flex;justify-content:space-between;align-items:center;margin-bottom:30px}.modal-container .create-user .header-form h3{color:var(--primary);font-weight:bold}.modal-container .create-user .header-form .btn-close{width:20px;height:20px;display:flex;justify-content:center;align-items:center;border-radius:50%;background-color:var(--primary)}.modal-container .create-user .container-values{width:100%;display:flex}.modal-container .create-user .btn-add{width:67px;height:35px;margin-top:20px;border-radius:5px;font-weight:bold;font-size:14px;line-height:17px;background-color:var(--primary);color:var(--default);text-align:center;align-self:flex-end}

